<template>
  <div>
    <van-search
      v-model="searchKeyword"
      placeholder="请输入主办方关键词"
      @search="onSearch"
      @clear="onSearch"
    />
    <div class="gap"></div>
    <div class="container">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div
          class="section"
          :class="{ 'one-style': index === 0 }"
          v-for="(item, index) in hostList"
          :key="item.id"
          @click="handleSelect(item.id, item.name)"
        >
          <span class="char">{{ item.name }}</span>
        </div>
      </van-list>
    </div>
    <div class="no-more">没有更多了~</div>

    <div class="bottom-gap"></div>
    <div class="btn-bottom" @click="handleAdd">新建</div>
  </div>
</template>

<script>
import bus from "@/utils/bus.js";

export default {
  components: {},
  data() {
    return {
      // 搜索关键字
      searchKeyword: "",
      // 每页显示条数
      limit: 10,
      // 当前页码
      pageNo: 1,
      hostList: [],
      loading: false,
      finished: false,
    };
  },
  created() {},

  methods: {
    handleSelect(id, name) {
      bus.$emit("hostObj", { id, name });

      // this.$route.params.hid = id;
      // this.$route.params.name = name;
      this.$router.back();
    },
    /**
     * 主办方列表
     * @param {Number} page 当前页码
     * @param {Number} limit 每页显示条数
     * @param {Number} keyword 搜索关键字
     */
    async hostInfoSearchAPI(page, limit, keyword) {
      const aid = this.$store.getters.associationInfo.id;
      const data = { aid, page, limit, name: keyword };
      return await this.$request("host.hostInfoSearch", data);
      // console.log("主办方列表 :>> ", typeof res);
      // this.hostList = handleSortReverse(res);
    },

    handleAdd() {
      this.$router.push({
        path: "/adminAddtUnit",
      });
    },

    onSearch() {
      this.hostList = [];
      this.pageNo = 1;
      this.finished = false;
      this.onLoad();
    },

    async onLoad() {
      console.log("this.searchKeyword :>> ", this.searchKeyword);
      const res = await this.hostInfoSearchAPI(
        this.pageNo,
        this.limit,
        this.searchKeyword
      );

      for (const item of res.data) {
        console.log("item :>> ", item);
        this.hostList.push(item);
      }

      this.pageNo += 1;

      // 加载状态结束
      this.loading = false;

      // 数据全部加载完成
      if (this.pageNo > res.last_page) {
        this.finished = true;
      }
    },
  },
};
</script>

